<template>
    <div class="icons">
        <swiper  :options="swiperOption">

        <swiper-slide  v-for="(page,index) in pages" :key="index">
        <div class="icon" v-for="item in page" :key="item.id">
            <div class="icon-img">
            <img class="iconii" :src="item.imgUrl" alt="">
            </div>
            <p class="icondes">{{ item.desc }}</p>
        </div>
       </swiper-slide>

      
        </swiper>
    </div>
</template>
<script>
export default {
    name:'HomeIcons',
    props:{list:Array},
    data() {
        return {
            swiperOption:{
                autoplay:false
            }
        }
    },
    computed:{
        pages(){
            const pages = []
            this.list.forEach((item,index)=>{
               const page = Math.floor(index/8)
                if(!pages[page]){
                    pages[page]=[]
                }
                pages[page].push(item)

            })
            return pages
        }
    }
}
</script>

 <style lang="stylus" scoped>
 @import '~styles/mixins.styl'
    .icons >>> .swiper-container
        height 0
        padding-bottom 42%
        
    .icon
      position relative
      overflow hidden
      float left 
      width 25% 
      height 0
      padding-bottom 21.3%
    
      .icon-img
        position absolute
        top 0
        left 0
        right 0
        bottom 0.44rem
        
        box-sizing border-border-box
        padding .1rem
        .iconii 
            height 100%
            display block
            margin 0 auto
      .icondes
            position absolute
            left 0
            right 0
            bottom 0
            height .44rem
            line-height .44rem
            color #333
            text-align center
            ellipsis()
                        



</style>



